body {
  background: url(../54433.jpg);
  background-repeat: no-repeat;
  background-size: 100%;
  width: 100%;
}

.highlight {
  background-color: #ffeb3b; /* Яркий желтый цвет для выделения */
}
h2 {
  text-transform: capitalize;
  width: 170px;
  padding: 5px 10px;
  text-align: center;
}
.highlight-row {
  background-color: rgba(
    67,
    75,
    67,
    0.3
  ) !important; /* Цвет выделенной строки */
}
.time {
  font-size: 20px;
  display: flex;
}
.title {
  font-size: 18px;
  font-weight: bold;
}
.developer {
  right: 20px;
  color: black;
  font-weight: bold;
}
.total {
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;

  font-weight: bold;
  text-decoration: underline;
  margin-left: 10px;
}
.statistic {
  width: 500px;
  text-shadow: -1px 2px 2px rgb(0 0 0);
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.time-profile {
  font-size: 17px;
  display: flex;
  background: rgb(0 0 0 / 21%);
  padding: 5px;
}
.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
}
.order {
  display: flex;
  border-top: 1px solid red;
  align-items: center;
  background: radial-gradient(gold, transparent);
  justify-content: center;
}
.order:first-child {
  border-top: none;
}
.order p {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 19px;
  color: rgb(59, 20, 20);
  padding: 5px 10px;
  width: 100%;
  max-width: 250px;
  font-weight: bold;
}
.select {
  width: 170px;
}
.orderZa {
  border: 10px solid gold;
  max-width: 300px;
  text-align: center;
  margin-left: 50px;
}
.ula {
  padding: 0;
}
.order span {
  border-top: 2px solid black;
  margin-top: 5px;
  font-size: 24px;
  color: #fff;
}
.arenda {
  margin: 0 auto;
  width: 350px;
  color: #fff;
  text-transform: none;
  font-size: 25px;
  background: gold;
}
.bam-servis {
  margin: 0 auto;
  width: 100%;
  text-shadow: -4px 0px 0px black;
  color: #fff;
  text-decoration: underline;
  font-size: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}
.green-span {
  height: 30px;
  width: 100px;
  background-color: green;
  display: block;
  margin-right: 5px;
}

.entry-block {
  text-align: center;
  display: flex;
  text-shadow: -1px 2px 2px rgb(0 0 0);
  color: #fff;
  align-items: center;
  font-weight: bold;
  font-size: 20px;
}

.red-span {
  height: 30px;
  width: 100px;
  background-color: red;
  display: block;
  margin-right: 5px;
}
.gold-span {
  height: 30px;
  width: 100px;
  background-color: gold;
  display: block;
  margin-right: 5px;
}
.yellow-span {
  height: 30px;
  width: 100px;
  background-color: yellow;
  margin-right: 5px;

  display: block;
}
.flex {
  display: flex;
  align-items: center;
  justify-content: center;
}
.login h1 {
  text-transform: none;
  text-align: center;
  padding: 15px;
}
h1 {
  width: 300px;
  color: #fff;
  display: flex;
  border-radius: 50px;
  background: #916767b0;
  text-transform: capitalize;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  border: 1px solid;
}
button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 5px 10px;
  margin: 5px;
  border-radius: 20px;
  cursor: pointer;
  font-size: 14px;
}

button:hover {
  background-color: #0056b3;
}

/* Стиль для выделенных строк в таблице */
tr.highlight {
  background-color: #ffeb3b;
}

/* Стиль для текущего дня */
.is-today {
  border: 2px solid red;
  width: 170px;
  padding: 5px 10px;
  text-align: center;
}
textarea {
  padding: 10px;
  margin: 5px;
  font-size: 15px;
  border-radius: 15px;
  border: 1px solid #ccc;
  width: 150px;
  text-align: center;
}
.canvas {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
#reload_href {
  display: flex;
  align-items: center;
  justify-content: center;
}
.login {
  display: flex;
  flex-direction: column;
  align-items: center;
  transform: translateY(5%);
}
.login h2 {
  color: black;
  font-size: 25px;
  text-transform: none;

  font-weight: bold;
}
.login input {
  width: 225px !important;
}
.login button {
  width: 150px !important;
  height: 35px;
}
.login form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
/* Стили для инпутов */
input[type="date"],
input[type="text"],
input[type="number"],
input[type="password"],
select {
  text-align: center;
  padding: 5px;
  margin: 5px;
  width: 100px;
  border-radius: 15px;
  border: 1px solid #ccc;
}

input[type="checkbox"] {
  margin: 0 5px;
}

/* Стили для таблицы */
table {
  width: 100%;
  color: black;
  background-color: #fff;
  border-collapse: collapse;
  margin-bottom: 20px;
}

th,
td {
  border: 1px solid #ddd;
  text-align: center;
  width: 50px !important;
}

th {
  background-color: #f4f4f4;
  text-align: center;
  width: 50px !important;
}
.today-class {
  background-color: yellow; /* или любой другой стиль, который вам нравится */
  font-weight: bold;
}
.tooltip {
  position: absolute;
  background-color: #333;
  color: #fff;
  padding: 5px;
  border-radius: 4px;
  font-size: 12px;
}
.nav-profile {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
}
.drivers-new {
  padding: 10px !important;
  width: 150px !important;
}
.sticky {
  position: sticky;
  top: 0;
  height: 70px;
  background-color: white; /* Фон для заголовков */
  z-index: 1; /* Чтобы заголовки были над содержимым */
  border-bottom: 2px solid #ccc; /* Для визуального разделения */
  text-align: center;
}
.username {
  font-size: 20px;
  font-weight: bold;
  color: #fff;
  border: 1px solid red;
  padding: 5px;
}
.month {
  margin-top: 20px;
}
.panel {
  font-size: 20px;
  font-weight: bold;
}
/* Спиннер-контейнер для центрирования */
.spinner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Занимает всю высоту экрана */
}

/* Анимация спиннера */
.spinner {
  border: 8px solid rgba(0, 0, 0, 0.1); /* Легкий фон */
  border-left-color: #4caf50; /* Цвет вращающейся линии */
  border-radius: 50%;
  width: 80px;
  height: 80px;
  animation: spin 1s linear infinite; /* Бесконечное вращение */
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.logo-login {
  background-image: url(../123.png);
  width: 200px;
  height: 189px;
  background-size: contain;
  background-repeat: no-repeat;
  border-radius: 80px;
}
@media (max-width: 800px) {
  .login h1 {
    color: #fff;
  }
  .login h2 {
    color: #fff;
  }
  .nav-profile {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .developer {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .bam-servis {
    text-align: center;
  }
  .wrapper {
    flex-direction: column;
  }
  .month {
    display: none;
    flex-direction: row;
  }
  .sybarenda {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .baza {
    display: none;
    flex-direction: column;
    align-items: center;
  }
  .entrys {
    display: flex;
    margin-bottom: 20px;
    flex-direction: column;
    align-items: center;
  }
  .driv {
    display: none;
    flex-direction: column;
    align-items: center;
  }
  .statistic {
    display: none;
    flex-direction: column;
    text-shadow: -4px 0px 0px black;
    color: #fff;
    align-items: center;
  }
  .entry-block {
    display: none;
    margin-bottom: 15px;
  }
  body {
    padding: 0;
    background-size: cover;
    height: 100vh;
  }
}
